
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import {mapState} from 'vuex'
export default {
name:"foot",
components:{
Tabbar,
TabbarItem
},
data(){
return{
selected:0,
}
},
computed:{
...mapState(['isHeaderFooterShow'])
},
watch:{
selected(val,oldVal){
if(val == 0){
this.$router.push('/')
}else if(val == 1){
this.$router.push('/search')
}
},
'$route' (to,from){
if(to.path == "/"){
this.selected =0
}else if(to.path == "/search"){
this.selected =1
}

}
},
mounted(){

let _this =this
_this.bus.$on('change',function(msg){
_this.show=msg;
})
},
}



